import { createSelector } from 'reselect'
import * as R from 'ramda'
import artboardSelectors, {
  getLayers as getLayersForArtboard
} from 'redux/modules/entities/artboards/selectors'
import {
  getLayer,
  getDestinationArtboardId
} from 'redux/modules/entities/layers/selectors'
import revisionSelectors, {
  getArtboardRevision
} from 'redux/modules/revisions/selectors'
import createArtboardUrl from 'utils/createArtboardUrl'

const relatedArtboardIdsSelector = createSelector(
  (state, props) => props.match.params.artboardId,
  state => state.entities.artboards,
  state => state.entities.layers,
  (currentArtboardId, artboards, layers) => {
    const layerIds = getLayersForArtboard(currentArtboardId, artboards)
    const relatedArtboardIds = layerIds
      .map(layerId => {
        return getDestinationArtboardId(layerId, layers)
      })
      .filter(idOrNull => idOrNull)
    return R.uniq(relatedArtboardIds)
  }
)

export const getArtboardUrls = createSelector(
  state => state.connection.token,
  (state, props) =>
    artboardSelectors.getArtboard(props.match.params.artboardId, state),
  (state, props) =>
    revisionSelectors.getArtboardRevision(props.match.params.artboardId, state),
  (token, artboard, revision) => {
    return R.map(
      scale => createArtboardUrl(artboard.id, token, scale, revision),
      R.range(1, 4)
    )
  }
)

export const getLayers = createSelector(
  (state, props) =>
    artboardSelectors.getArtboard(props.match.params.artboardId, state),
  state => state.entities.layers,
  (artboard, layers) => {
    return artboard
      ? artboard.layers.map(layerId => getLayer(layerId, layers))
      : []
  }
)

export const getRelatedArtboardUrls = createSelector(
  relatedArtboardIdsSelector,
  state => state.connection.token,
  state => state.revisions,
  (artboardIds, token, revisions) => {
    return artboardIds.map(artboardId => {
      const revision = getArtboardRevision(artboardId, revisions)
      return R.range(1, 4).map(scale =>
        createArtboardUrl(artboardId, token, scale, revision)
      )
    })
  }
)



