import { push, goBack } from 'react-router-redux'
import { withHandlers, branch, renderNothing } from 'recompose'
import * as R from 'ramda'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import HitArea from 'components/HitArea'
import { getHitAreaDetails } from './selectors'

const mapDispatchToProps = (dispatch, props) => ({
  goToArtboard: (artboardId, animationType) => {
    dispatch(
      push({
        pathname: `/${artboardId}`,
        state: {
          transitionType: animationType,
          fromArtboardId: props.match.params.artboardId,
          toArtboardId: artboardId
        }
      })
    )
  },
  goToPreviousArtboard: () => {
    dispatch(goBack())
  }
})

const enhance = R.compose(
  withHandlers({
    onClick: ({ flow, goToArtboard, goToPreviousArtboard }) => e => {
      e.stopPropagation()
      if (flow.isBackAction) {
        goToPreviousArtboard()
      } else if (flow.destinationArtboardID) {
        goToArtboard(flow.destinationArtboardID, flow.animationType)
      }
    }
  }),
  branch(
    ({ flow, canGoBack }) => flow && flow.isBackAction && !canGoBack,
    renderNothing
  )
)

export default R.compose(
  withRouter,
  connect(getHitAreaDetails, mapDispatchToProps),
  enhance
)(HitArea)



