import globalizeSelectors from 'utils/globalizeSelectors'
import * as R from 'ramda'

const localState = R.path(['entities', 'artboards'])

export const getArtboard = (id, state) => R.pathOr(null, ['byId', id], state)

export const getLayers = (id, state) =>
  R.propOr([], 'layers', getArtboard(id, state))

export const getViewport = (artboardId, state) =>
  R.propOr(
    { width: null, height: null },
    'viewport',
    getArtboard(artboardId, state)
  )

export default globalizeSelectors(localState, {
  getArtboard,
  getLayers,
  getViewport
})



