import React from 'react'
import styled from 'styled-components'
import { compose, withState, withHandlers } from 'recompose'
import { floatsToCSSRGBAString } from '@awkward/sketch-utils'

const ArtboardContainer = styled.div`
  box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 4px 0px;
  position: relative;
  width: ${props => props.width};
  height: ${props => props.height};
  background-color: ${props => (props.imageLoaded ? '#fff' : 'transparent')};
  overflow: hidden;
`

const ArtboardImage = styled.img`
  display: block;
  background-color: ${props => props.backgroundColor};
  user-select: none;
  position: absolute;
  top: 0;
  left: 0;
  user-select: none;
`

const Artboard = ({
  className,
  style,
  layers,
  imageSrc,
  imageSrcSet,
  width,
  height,
  onImageLoad,
  imageLoaded,
  backgroundColor,
  children
}) => (
  <ArtboardContainer
    className={className}
    style={style}
    width={width}
    height={height}
    imageLoaded={imageLoaded}>
    <ArtboardImage
      width={width}
      height={height}
      draggable={false}
      src={imageSrc}
      srcSet={imageSrcSet}
      onLoad={onImageLoad}
      backgroundColor={(() => {
        if (!imageLoaded) return 'transparent'

        return backgroundColor
          ? floatsToCSSRGBAString(backgroundColor)
          : 'white'
      })()}
    />
    {children}
  </ArtboardContainer>
)

const enhance = compose(
  withState('imageLoaded', 'setImageLoaded', false),
  withHandlers({
    onImageLoad: ({ setImageLoaded }) => () => {
      setImageLoaded(true)
    }
  })
)

export default enhance(Artboard)



